.twim-widget-controls {
  margin-bottom: 15px;
}

.twim-tabs {
  width: 100%;
  padding: 5px;
}

p.twim-andor {
  display: flex;
  align-items: center;
  gap: 0 5px;
  padding: 0 5px;
}

.twim-tab-content {
  padding: 5px;
}

.twim-tab-nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2px;
  margin-bottom: 0;
  border-top: 1px solid;
  border-bottom: 1px solid;
  padding: 0 5px;
}
.twim-tab-nav::-webkit-scrollbar {
  height: 8px;
}
.twim-tab-nav::-webkit-scrollbar-thumb {
  border-radius: 4px;
}
.twim-tab-nav li {
  justify-content: center;
  list-style: none;
  padding: 5px 4px;
  background: #eee;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-bottom: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 5px 5px 0 0;
  border-top: 1px solid gray;
  border-right: 1px solid gray;
}
.twim-tab-nav li:hover {
  color: white;
}
.twim-tab-nav li.active {
  color: white;
}
.twim-tab-nav li.has-settings:after {
  font-family: dashicons;
  font-size: 120%;
  padding-left: 4px;
}
.twim-tab-nav li.has-settings.setting-show:after {
  content: "\f177";
}
.twim-tab-nav li.has-settings.setting-hide:after {
  content: "\f530";
}

